import React, { Component } from 'react';
import ReactEcharts from '../..';

export default class Gauge extends Component {
  constructor(props) {
    super(props);
    this.state = this.getInitialState();
  }
  getOption = () => {
    return {
    backgroundColor: '#1b1b1b',
    tooltip : {
      formatter: "{a} <br/>{c} {b}"
    },
    toolbox: {
      show : true,
      feature : {
        mark : {show: true},
        restore : {show: true},
        saveAsImage : {show: true}
      }
    },
    series : [
      {
        name:'',
        type:'gauge',
        min:0,
        max:220,
        splitNumber:11,
        radius: '50%',
        axisLine: {      // 
          lineStyle: {     // lineStyle
            color: [[0.09, 'lime'],[0.82, '#1e90ff'],[1, '#ff4500']],
            width: 3,
            shadowColor : '#fff', //
            shadowBlur: 10
          }
        },
        axisLabel: {      // 
          textStyle: {     // lineStyle
            fontWeight: 'bolder',
            color: '#fff',
            shadowColor : '#fff', //
            shadowBlur: 10
          }
        },
        axisTick: {      // 
          length :15,    // length
          lineStyle: {     // lineStyle
            color: 'auto',
            shadowColor : '#fff', //
            shadowBlur: 10
          }
        },
        splitLine: {       // 
          length :25,     // length
          lineStyle: {     // lineStylelineStyle
            width:3,
            color: '#fff',
            shadowColor : '#fff', //
            shadowBlur: 10
          }
        },
        pointer: {       // 
          shadowColor : '#fff', //
          shadowBlur: 5
        },
        title : {
          textStyle: {     // TEXTSTYLE
            fontWeight: 'bolder',
            fontSize: 20,
            fontStyle: 'italic',
            color: '#fff',
            shadowColor : '#fff', //
            shadowBlur: 10
          }
              },
              detail : {
                  backgroundColor: 'rgba(30,144,255,0.8)',
                  borderWidth: 1,
                  borderColor: '#fff',
                  shadowColor : '#fff', //
                  shadowBlur: 5,
                  offsetCenter: [0, '50%'],       // x, ypx
                  textStyle: {       // TEXTSTYLE
                      fontWeight: 'bolder',
                      color: '#fff'
                  }
              },
              data:[{value: 40, name: 'km/h'}]
          },
          {
              name:'',
              type:'gauge',
              center : ['25%', '55%'],    // 
              radius : '30%',
              min:0,
              max:7,
              endAngle:45,
              splitNumber:7,
              axisLine: {            // 
                  lineStyle: {       // lineStyle
                      color: [[0.29, 'lime'],[0.86, '#1e90ff'],[1, '#ff4500']],
                      width: 2,
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  }
              },
              axisLabel: {            // 
                  textStyle: {       // lineStyle
                      fontWeight: 'bolder',
                      color: '#fff',
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  }
              },
              axisTick: {            // 
                  length :12,        // length
                  lineStyle: {       // lineStyle
                      color: 'auto',
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  }
              },
              splitLine: {           // 
                  length :20,         // length
                  lineStyle: {       // lineStylelineStyle
                      width:3,
                      color: '#fff',
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  }
              },
              pointer: {
                  width:5,
                  shadowColor : '#fff', //
                  shadowBlur: 5
              },
              title : {
                  offsetCenter: [0, '-30%'],       // x, ypx
                  textStyle: {       // TEXTSTYLE
                      fontWeight: 'bolder',
                      fontStyle: 'italic',
                      color: '#fff',
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  }
              },
              detail : {
                  //backgroundColor: 'rgba(30,144,255,0.8)',
                 // borderWidth: 1,
                  borderColor: '#fff',
                  shadowColor : '#fff', //
                  shadowBlur: 5,
                  width: 80,
                  height:30,
                  offsetCenter: [25, '20%'],       // x, ypx
                  textStyle: {       // TEXTSTYLE
                      fontWeight: 'bolder',
                      color: '#fff'
                  }
              },
              data:[{value: 1.5, name: 'x1000 r/min'}]
          },
          {
              name:'',
              type:'gauge',
              center : ['75%', '50%'],    // 
              radius : '30%',
              min:0,
              max:2,
              startAngle:135,
              endAngle:45,
              splitNumber:2,
              axisLine: {            // 
                  lineStyle: {       // lineStyle
                      color: [[0.2, 'lime'],[0.8, '#1e90ff'],[1, '#ff4500']],
                      width: 2,
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  }
              },
              axisTick: {            // 
                  length :12,        // length
                  lineStyle: {       // lineStyle
                      color: 'auto',
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  }
              },
              axisLabel: {
                  textStyle: {       // lineStyle
                      fontWeight: 'bolder',
                      color: '#fff',
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  },
                  formatter:function(v){
                      switch (v + '') {
                          case '0' : return 'E';
                          case '1' : return 'Gas';
                          case '2' : return 'F';
                      }
                  }
              },
              splitLine: {           // 
                  length :15,         // length
                  lineStyle: {       // lineStylelineStyle
                      width:3,
                      color: '#fff',
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  }
              },
              pointer: {
                  width:2,
                  shadowColor : '#fff', //
                  shadowBlur: 5
              },
              title : {
                  show: false
              },
              detail : {
                  show: false
              },
              data:[{value: 0.5, name: 'gas'}]
          },
          {
              name:'',
              type:'gauge',
              center : ['75%', '50%'],    // 
              radius : '30%',
              min:0,
              max:2,
              startAngle:315,
              endAngle:225,
              splitNumber:2,
              axisLine: {            // 
                  lineStyle: {       // lineStyle
                      color: [[0.2, 'lime'],[0.8, '#1e90ff'],[1, '#ff4500']],
                      width: 2,
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  }
              },
              axisTick: {            // 
                  show: false
              },
              axisLabel: {
                  textStyle: {       // lineStyle
                      fontWeight: 'bolder',
                      color: '#fff',
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  },
                  formatter:function(v){
                      switch (v + '') {
                          case '0' : return 'H';
                          case '1' : return 'Water';
                          case '2' : return 'C';
                      }
                  }
              },
              splitLine: {           // 
                  length :15,         // length
                  lineStyle: {       // lineStylelineStyle
                      width:3,
                      color: '#fff',
                      shadowColor : '#fff', //
                      shadowBlur: 10
                  }
              },
              pointer: {
                  width:2,
                  shadowColor : '#fff', //
                  shadowBlur: 5
              },
              title : {
                  show: false
              },
              detail : {
                  show: false
              },
              data:[{value: 0.5, name: 'gas'}]
          }
      ]
    };
  };

  timeTicket = null;
  getInitialState = () => ({option: this.getOption()});

  componentDidMount() {
    if (this.timeTicket) {
      clearInterval(this.timeTicket);
    }
    this.timeTicket = setInterval(() => {
      const option = this.state.option;
      option.series[0].data[0].value = (Math.random()*100).toFixed(2) - 0;
      option.series[1].data[0].value = (Math.random()*7).toFixed(2) - 0;
      option.series[2].data[0].value = (Math.random()*2).toFixed(2) - 0;
      option.series[3].data[0].value = (Math.random()*2).toFixed(2) - 0;
      this.setState({option: option});
    }, 1000);
  };

  componentWillUnmount() {
    if (this.timeTicket) {
      clearInterval(this.timeTicket);
    }
  };

  render() {
    return (
      <div className='examples'>
        <div className='parent'>
          <label> render a car gauge chart. </label>
          <ReactEcharts
            option={this.state.option}
            style={{height: '500px', width: '100%'}}
            className='react_for_echarts' />
        </div>
    </div>
    );
  }
}



