import React, { PureComponent } from 'react';
import ReactEcharts from '../..';

export default class Simple extends PureComponent {
  getOption = () => {
    return {
      title: {
        text: ''
      },
      tooltip : {
        trigger: 'axis'
      },
      legend: {
        data:['','','']
      },
      toolbox: {
        feature: {
          saveAsImage: {}
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis : [
        {
          type : 'category',
          boundaryGap : false,
          data : ['','','','','','','']
        }
      ],
      yAxis : [
        {
          type : 'value'
        }
      ],
      series : [
        {
          name:'',
          type:'line',
          stack: '',
          areaStyle: {normal: {}},
          data:[120, 132, 101, 134, 90, 230, 210]
        },
        {
          name:'',
          type:'line',
          stack: '',
          areaStyle: {normal: {}},
          data:[220, 182, 191, 234, 290, 330, 310]
        },
        {
          name:'',
          type:'line',
          stack: '',
          areaStyle: {normal: {}},
          data:[150, 232, 201, 154, 190, 330, 410]
        }
      ]
    };
  };
  render() {
    let code = "<ReactEcharts \n" +
          "  option={this.getOption()} \n" +
          "  style={{height: '350px', width: '100%'}}  \n" +
          "  className='react_for_echarts' />";
    return (
      <div className='examples'>
        <div className='parent'>
          <label> render a Simple echart With <strong>option and height</strong>: </label>
          <ReactEcharts
            option={this.getOption()}
            style={{height: '350px', width: '100%'}}
            className='react_for_echarts' />
          <label> code below: </label>
          <pre>
            <code>{code}</code>
          </pre>
        </div>
      </div>
    );
  }
}



