import React, { PureComponent } from 'react';
import ReactEcharts from '../..';

export default class Api extends PureComponent {
  getOption = () => ({
    title: {
      text: '',
      subtext: ''
    },
    tooltip: {
      trigger: 'item',
      formatter: "{a} <br/>{b} : {c}%"
    },
    toolbox: {
      feature: {
        dataView: {readOnly: false},
        restore: {},
        saveAsImage: {}
      }
    },
    legend: {
      data: ['','','','','']
    },
    series: [
      {
        name: '',
        type: 'funnel',
        left: '10%',
        width: '80%',
        label: {
          normal: {
            formatter: '{b}'
          },
          emphasis: {
            position:'inside',
            formatter: '{b}: {c}%'
          }
        },
        labelLine: {
          normal: {
            show: false
          }
        },
        itemStyle: {
          normal: {
            opacity: 0.7
          }
        },
        data: [
          {value: 60, name: ''},
          {value: 40, name: ''},
          {value: 20, name: ''},
          {value: 80, name: ''},
          {value: 100, name: ''}
        ]
      },
      {
        name: '',
        type: 'funnel',
        left: '10%',
        width: '80%',
        maxSize: '80%',
        label: {
          normal: {
            position: 'inside',
            formatter: '{c}%',
            textStyle: {
              color: '#fff'
            }
          },
          emphasis: {
            position:'inside',
            formatter: '{b}: {c}%'
          }
        },
        itemStyle: {
          normal: {
            opacity: 0.5,
            borderColor: '#fff',
            borderWidth: 2
          }
        },
        data: [
          {value: 30, name: ''},
          {value: 10, name: ''},
          {value: 5, name: ''},
          {value: 50, name: ''},
          {value: 80, name: ''}
        ]
      }
    ]
  });

  clickBtn = () => {
    console.log(this.echarts_react.getEchartsInstance().getDataURL());
    console.log(this.echarts_react);
    window.open(this.echarts_react.getEchartsInstance().getDataURL(), '_blank');
  };

  render() {
    let code = "<ReactEcharts ref={(e) => { this.echarts_react = e; }} \n" +
          "  option={this.getOption()} /> \n" +
          "\n" +
          "// use echarts API: http://echarts.baidu.com/api.html#echartsInstance" +
          "this.echarts_react.getEchartsInstance().getDataURL();";
    return (
      <div className='examples'>
        <div className='parent'>
          <label> use echarts API With <strong> getEchartsInstance() </strong>: (the API will return the echarts instance, then you can use any API of echarts.)</label>
          <ReactEcharts ref={(e) => { this.echarts_react = e; }}
            option={this.getOption()}  />
          <div>
            <button className='a_line' onClick={this.clickBtn}>click here to get the DataURL of chart.</button>
          </div>
          <label> code below: (echarts API list see: http://echarts.baidu.com/api.html#echartsInstance)</label>
          <pre>
            <code>{code}</code>
          </pre>
        </div>
      </div>
    );
  }
}



