import React, { PureComponent } from 'react';
import ReactEcharts from '../..';

export default class Loading extends PureComponent {
  _t = null;
  getOption = () => {
    return {
      title: {
        text: ''
      },
      tooltip: {},
      legend: {
        data: ['Allocated Budget', 'Actual Spending']
      },
      radar: {
        // shape: 'circle',
        indicator: [
           { name: 'sales', max: 6500},
           { name: 'Administration', max: 16000},
           { name: 'Information Techology', max: 30000},
           { name: 'Customer Support', max: 38000},
           { name: 'Development', max: 52000},
           { name: 'Marketing', max: 25000}
        ]
      },
      series: [{
        name: ' vs Budget vs spending',
        type: 'radar',
        // areaStyle: {normal: {}},
        data : [
          {
            value : [4300, 10000, 28000, 35000, 50000, 19000],
            name : 'Allocated Budget'
          },
           {
            value : [5000, 14000, 28000, 31000, 42000, 21000],
            name : 'Actual Spending'
          }
        ]
      }]
    };
  };
  onChartReady = (chart) => {
    this._t = setTimeout(function() {
      chart.hideLoading();
    }, 3000);
  };

  getLoadingOption = () => {
    return {
      text: '...',
      color: '#4413c2',
      textColor: '#270240',
      maskColor: 'rgba(194, 88, 86, 0.3)',
      zlevel: 0
    };
  };

  componentWillUnmount() {
    clearTimeout(this._t);
  };

  render() {
    let code = "onChartReady: function(chart) {\n" +
           "  'chart.hideLoading();\n" +
           "}\n\n" +
           "<ReactEcharts \n" +
          "  option={this.getOption()} \n" +
          "  onChartReady={this.onChartReady} \n" +
          "  loadingOption={this.getLoadingOption()} \n" +
          "  showLoading={true} />";

    return (
      <div className='examples'>
        <div className='parent'>
          <label> Chart loading With <strong> showLoading </strong>: (when chart ready, hide the loading mask.)</label>
          <ReactEcharts
            option={this.getOption()}
            onChartReady={this.onChartReady}
            loadingOption={this.getLoadingOption()}
            showLoading={true} />
          <label> code below: </label>
          <pre>
            <code>{code}</code>
          </pre>
        </div>
      </div>
    );
  }
}



