import React, { PureComponent } from 'react';
import ReactEcharts from '../..';

import echarts from 'echarts';

export default class Theme extends PureComponent {
  getOption = () => {
    return {
      title: {
        text: '',
        subtext: 'From ExcelHome',
        sublink: 'http://e.weibo.com/1341556070/Aj1J2x5a5'
      },
      tooltip : {
        trigger: 'axis',
        axisPointer : {      // 
          type : 'shadow'    // 'line' | 'shadow'
        }
      },
      legend: {
        data:['','']
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: {
        type : 'category',
        splitLine: {show:false},
        data :  ["111", "112", "113", "114", "115", "116", "117", "118", "119", "1110", "1111"]
      },
      yAxis: {
        type : 'value'
      },
      series: [
        {
          name: '',
          type: 'bar',
          stack: '',
          itemStyle: {
            normal: {
              barBorderColor: 'rgba(0,0,0,0)',
              color: 'rgba(0,0,0,0)'
            },
            emphasis: {
              barBorderColor: 'rgba(0,0,0,0)',
              color: 'rgba(0,0,0,0)'
            }
          },
          data: [0, 900, 1245, 1530, 1376, 1376, 1511, 1689, 1856, 1495, 1292]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            normal: {
              show: true,
              position: 'top'
            }
          },
          data: [900, 345, 393, '-', '-', 135, 178, 286, '-', '-', '-']
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            normal: {
              show: true,
              position: 'bottom'
            }
          },
          data: ['-', '-', '-', 108, 154, '-', '-', '-', 119, 361, 203]
        }
      ]
    };
  };

  registerTheme() {
    echarts.registerTheme('my_theme', {
      backgroundColor: '#f4cccc'
    });
  };
  render() {
    this.registerTheme();
    let code = "echarts.registerTheme('my_theme', {\n" +
           "  backgroundColor: '#f4cccc'\n" +
           "});\n\n" +
           "<ReactEcharts \n" +
          "  option={this.getOption()} \n" +
          "  theme='my_theme' />";
    return (
      <div className='examples'>
        <div className='parent'>
          <label> render a echart With <strong>theme</strong>, should <strong>echarts.registerTheme(themeName, themeObj)</strong> before use.</label>
          <ReactEcharts
            option={this.getOption()}
            theme="my_theme" />
          <label> the theme object format: https://github.com/ecomfe/echarts/blob/master/theme/dark.js</label>
          <pre>
            <code>{code}</code>
          </pre>
        </div>
      </div>
    );
  }
}



